import React, { PureComponent, Suspense } from "react";
import { CloudUrl, tagembedUrl } from "../../../constants";
import { isTagShop, isEmbed, isSocialWall, TagembedUrl } from "../../../actions/api";
import { AppUrl } from "../../../constants";
import { formatUnixTimestamp } from "../../../utils/Common";
import './scss/Error.scss';
const OnsiteUploadButton = React.lazy(() => import("../UserComponents/OnsiteUploadButton"));
const ErrorMessage = React.lazy(() => import("./Elements/ErrorMessage"));
const ErrorWithBranding = React.lazy(() => import('./Elements/ErrorWithBranding'));

 const ERROR_ERROR = { 1: "@", 2: "#", 3: "List ", 4: "@", 5: "Advanced ", 6: "Location ", 7: "@", 8: "Page ", 9: "Page ", 10: "Group ", 11: "Playlist ", 12: "Board ", 13: "URL ", 14: "Event ", 16: "Mention ", 22: "Workplace ", 23: "@", 24: "@", 25: "@", 26: "#", 65: "Album", 67: "Page", 53: "Group ", 54: "Topic ", 55: "Page", 33: "Place ", 63: "Search ", 64: "Sticker " };
 
 const ErrorType = React.memo(({ error, wall }) => {
  let err = error.errorWithMessage;
  let editor = window.editor ? true : window.location.pathname.includes('/editor');
  const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed" || tagembedUrl;
  if (err) return err && Object.keys(err).length ? err.type == "VISITOR_LIMIT_EXCEEDED" ?
    <Suspense fallback={<></>}><ErrorWithBranding title={'Page Views Limit Exceeded'} description={'Oops! Seems Page Views Limit for the plan exceeded. If you are the owner of this Taggbox account, please click below link to access your account.'} button={`<a href="${isTagEmbedStatus ? TagembedUrl : AppUrl}" target="_blank" class="tb_error_btn_primary">Update Now</a>`} /></Suspense>
     : <Suspense fallback={<></>}><ErrorWithBranding title={err?.message ? err.message : 'Wall Not exist'} description={'Oops! Seems like something went wrong. If you are the owner of this Taggbox account, please click below link to access your account.'} button={`<a href = "${isTagEmbedStatus ? TagembedUrl : AppUrl}" target = "_blank" class="tb_error_btn_primary"> Access Now</a > `} /> </Suspense>
    :  null
  else if (error.planLimit)
    return <Suspense fallback={<></>}><ErrorWithBranding title={`You're Running Extra Widgets/Feeds!`} description={'To enjoy uninterrupted services, kindly revoke the additional widget / feed or upgrade your plan.'} button={`<a href="${AppUrl}" target="_blank" class="tb_error_btn_primary">Update Now</a>`} /> </Suspense>

  else if (error.themeError && Object.keys(error.themeError).length) {
    switch (error.themeError.error_code) {
      case 201:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`If the Moderation is ON, approve the posts from the moderation section to make them appear on your widget. Else, there won’t be any posts on your added feeds.`} /></Suspense>;
      case 202:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Feeds!'} description={`Seems like you have not added any feed. Kindly add feed from your preferred social network`} /></Suspense>;
      case 203:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'Feed Not Activated!'} description={`Looks like your feed status is off. <a href="${AppUrl}/wall/index/${wall.Wall.id}" target="blank">Click here</a> to turn the status on! Or add another feed`} /></Suspense>;
      case 204:
      case 205:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Tweets/Posts!'} description={`Currently, No Tweets/Posts are available on the entered <div class="tb_error_highlight">${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}</div>. The new Tweets/Posts will start appearing here.`} /></Suspense>;
      case 206:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Tweets!'} description={`No Tweets are available on the entered <div class="tb_error_highlight">${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}</div> All the upcoming tweets will appear here. <br>The Tweets are older than your chosen ${error.themeError.older_post === 2 ? 7 : error.themeError.older_post === 1 ? 30 : null} days.`} /></Suspense>;
      case 207:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that all your posts are private. Please go to <a href="${AppUrl}moderation/index/${wall.Wall.id}" target="_blank" rel="noopener noreferrer">Moderation</a> and make them public.`} /></Suspense>;
      case 208:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that the available posts are older than your chosen ${error.themeError.older_days !== 8 ? error.themeError.older_days + ' Days' : formatUnixTimestamp(error.themeError.older_post)}. All the new posts will start to appear here.`} /></Suspense>;
      case 209:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`It appears that there are no posts available with images or videos. The theme doesn’t support text content.`} /></Suspense>;
      case 211:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`No posts are available on the entered ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days}. All Upcoming posts will appear here.`} /></Suspense>;
      case 212:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} editor={editor} title={'No Posts!'} description={`Only new posts from Instagram ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days} will appear here. Existing posts from Instagram ${ERROR_ERROR[error.themeError.older_post]} ${error.themeError.older_days} can not be retrieved.`} /></Suspense>;
      case 213:
        return <Suspense fallback={<></>}><ErrorMessage {...error.themeError} wall={wall} error={error} editor={editor} title={`Your Social ${isEmbed ? 'Embed' : ''} wall is Inactive.`} description={`To enhance your ${!isEmbed ? `event` : `website`} experience, re-activate your Social wall now`} utm={'feedinactive'} /></Suspense>;
      default:
        return ""
    }

  } else return err && Object.keys(err).length ? <ErrorMessage error={error} /> : null
})
export default class ErrorPage extends PureComponent {
  render() {
    const ErrorImage = { backgroundImage: `url(${CloudUrl}/media/error/nopost.png)` }
    const { error, wall } = this.props
    const queryParams = new URLSearchParams(window.location.search);
    const previewId = queryParams.get('preview');
    const isTagEmbedStatus = wall?.User?.userDatabaseType == "tagembed" || tagembedUrl;
    return (
      <>
        {previewId ? document.body.classList.add(`tb_previw-error`) : null}
        <div className={`tb_error_page ${!isEmbed ? 'tb_wall_error' : ''} tb_error_theme_${(wall != null && wall) ? wall?.ThemeInfo.id : 'wall_error'} ${isTagEmbedStatus ? 'tb_tagEmbed' : ''} ${isTagShop ? 'tb_tagshop' : ''}`}>
          <div className="tb_error_page_bg" style={ErrorImage}> </div>
          {(wall && Object.keys(wall).length && wall.UserRule.on_site_upload && wall?.SnapUp?.Form.status) ? <div className="tb_error_onsite_btn">
            <Suspense fallback={<></>}><OnsiteUploadButton SnapUp={wall.SnapUp} id={wall.Wall.id} tagShopWallId={wall?.ThemeInfo.wallId} /></Suspense>
          </div> : null}
          <ErrorType error={error} wall={wall} />
        </div>
      </>
    );
  }
}
